import React from "react";

const Button = (props) => {
    const { children, link, size, variant, blank, otherClass } = props;
    return <a href={`${link || "#"}`} target={blank && "_blank"} className={`
    ${size === "md" ? "w-full md:w-[305px]" : size === "lg" ? "w-full md:w-[362px]" : "w-full md:w-[298px]"}
    ${variant === "secondary" ? "bg-[#0C091F] hover:bg-[#0C091F]/80" : variant === "transparent" ? "border-2 border-white bg-transparent w-full md:w-[250px]" : "bg-gradient-to-l from-[#4D964F] to-[#259FA9] hover:bg-gradient-to-l hover:from-[#4D964F]/80 hover:to-[#259FA9]/80"}
    py-4 md:h-[70px] items-center rounded-full cursor-pointer flex justify-center text-lg md:text-xl font-bold text-white ${otherClass}`}>{children || "Button"}</a>;
};

export default Button;
