import React from "react";
// import { Link } from "react-router-dom";
import logo from "../../assets/images/logo.svg";
import { animateScroll as scroll, Link } from "react-scroll";

export const navLinks = [
    {
        name: "About",
        link: "about",
    },
    {
        name: "Whitepaper",
        link: "https://jukepaper.jukeboys.io/",
    },
    {
        name: "Roadmap",
        link: "roadmap",
    },
    {
        name: "Team",
        link: "team",
    },
    {
        name: "Faqs",
        link: "faqs",
    },
];

const NavbarComponent = () => {
    const [toggled, setToggled] = React.useState(false);

    const toggleNav = () => {
        setToggled(!toggled);
    }

    return (
        <nav className="bg-black/50 lg:bg-black/20 text-white fixed lg:sticky left-0 top-0 w-screen z-50">
            <div className="container max-w-7xl px-5 flex flex-col space-y-5 lg:space-y-0 lg:flex-row justify-between items-start lg:items-center py-5 lg:h-[159px] lg:py-0 text-lg">
                <img src={logo} alt="logo" onClick={() => scroll.scrollToTop()} className="cursor-pointer w-14 sm:w-auto sm:h-auto" />
                <span className="block absolute right-5 top-2 sm:top-3 lg:hidden" onClick={toggleNav}>
                    {
                        !toggled ? (
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-8 h-8 sm:w-10 sm:h-10">
                                <path strokeLinecap="round" strokeLinejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
                            </svg>
                        ) : (
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-8 h-8 sm:w-10 sm:h-10">
                                <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                            </svg>
                        )
                    }

                </span>


                <div className={`${toggled ? "block w-full pt-5 pb-10 grow" : "hidden"} lg:block`}>
                    <ul className="flex flex-col lg:flex-row items-center space-y-10 lg:space-y-0 lg:space-x-10">
                        {navLinks.map((link, index) => (
                            index === 1 ? (
                                <li key={index}>
                                    <a target={link.name === "Whitepaper" && "_blank"} href={link.link} className="block lg:inline-block lg:mt-0 text-xl lg:hover:transition lg:hover:delay-150 lg:hover:duration-300 lg:hover:ease-in-out lg:hover:scale-110 cursor-pointer">{link.name}</a>
                                </li>) : (
                                <Link
                                    activeClass="text-[#298ded] font-bold"
                                    to={link.link}
                                    className="block lg:inline-block lg:mt-0 text-xl lg:hover:transition lg:hover:delay-150 lg:hover:duration-300 lg:hover:ease-in-out lg:hover:scale-110 cursor-pointer"
                                    spy={true}
                                    smooth={true}
                                    offset={-70}
                                    duration={500}
                                    hashSpy={true}
                                    delay={500}
                                >
                                    {link.name}
                                </Link>
                            )
                        ))}
                    </ul>
                </div>
            </div>
        </nav>
    );
};

export default NavbarComponent;
