import React, { useCallback, useEffect } from "react";
import Button from "../components/ButtonComponent";
import chevronRight from "../assets/images/chevronRight.svg";
import UtilityCard from "../components/UtilityCard";
import { faqArray, teamArray, utilityArray } from "../data";
import TeamCard from "../components/TeamCard";
// import heroBg from "../assets/images/hero-bg.svg";
import roadmap from "../assets/images/roadmap.svg";
import greenLine from "../assets/images/green-line.svg";
import blueLine from "../assets/images/blue-line.svg";
import whiteLine from "../assets/images/white-line.svg";
import chevronLeft from "../assets/images/chevronLeft2.svg";
import chevronRight2 from "../assets/images/chevronRight2.svg";
import { AnimationOnScroll } from 'react-animation-on-scroll';
import emoji from "../assets/images/Emoji.svg";
import line2 from "../assets/images/line2.svg";

const LandingScreen = () => {
    const [currentSlide, setCurrentSlide] = React.useState(0);
    const [activeFAQ, setActiveFAQ] = React.useState(null);
    const utilityData = utilityArray;

    const faqs = faqArray;

    const teams = teamArray;

    const jacket = "https://res.cloudinary.com/hammy06/image/upload/v1663574307/jukedome/jacket_frxjsf.svg";
    const skate = "https://res.cloudinary.com/hammy06/image/upload/v1663574135/jukedome/skate_wjddat.svg";
    const boomBox = "https://res.cloudinary.com/hammy06/image/upload/v1663574137/jukedome/boombox_q8mxbg.svg";
    const bike = "https://res.cloudinary.com/hammy06/image/upload/v1663574943/jukedome/bike_nvthvm.svg";

    const slides = [boomBox, skate, bike, jacket]

    const nextSlide = useCallback(() => {
        if (currentSlide === (slides.length - 1)) {
            setCurrentSlide(0)
        } else {
            setCurrentSlide(prev => prev + 1)
        }
    }, [currentSlide, slides.length])

    const prevSlide = () => {
        if (currentSlide === 0) {
            setCurrentSlide((slides.length - 1))
        } else {
            setCurrentSlide(prev => prev - 1)
        }
    }

    useEffect(() => {
        const interval = setInterval(nextSlide, 3000);
        return () => clearInterval(interval)
    }, [nextSlide])

    return (
        <div className="text-white relative">
            <div
                className="h-screen grid place-items-center w-full bg-no-repeat bg-center relative" id="top"
                style={{ backgroundImage: "url(https://res.cloudinary.com/hammy06/image/upload/f_auto/v1663574578/jukedome/hero-bg_evzje3.svg)" }}
            >
                <div className="container max-w-7xl text-center px-5 pt-10 lg:pt-14">
                    <h1 className="text-[48px] lg:text-[80px] font-bold leading-[50px] lg:leading-[100px] mb-5 capitalize animate__animated animate__fadeInDown animate__delay-3s">
                        welcome <br /> to jukedome
                    </h1>

                    <p className="font-medium text-lg lg:text-2xl capitalize mb-10 animate__animated animate__fadeIn animate__delay-3s">
                        a new metaverse experience
                    </p>
                    <div className="mx-auto flex items-center justify-center space-x-5 sm:space-x-10 md:space-x-14">
                        <Button link="http://discord.gg/XkqYnRQstW" blank={true} otherClass="animate__animated animate__fadeInLeft animate__delay-4s">Join Discord</Button>
                        <Button variant="transparent" link="https://www.cyberpickle.io/redeem/jukeboys" blank={true} otherClass="animate__animated animate__fadeInRight animate__delay-4s">Redeem</Button>

                    </div>

                    <div className="absolute right-10 sm:right-16 lg:right-48 bottom-10 lg:bottom-auto lg:mt-5 animate__animated animate_fadeInDown">
                        <div className="flex items-center space-x-6 font-medium">
                            <img alt="" src={emoji} />
                            <p className="capitalize text-xl flex flex-shrink-0">party to earn</p>
                            <img alt="" src={line2} className="h-32 md:h-auto" />
                        </div>
                    </div>

                </div>
                <div className="sphere1">
                </div>
            </div>

            <div className="mb-[50px] mt-[100px] lg:mb-[400px] lg:mt-0" id="about">
                <div className="sphere2 z-0"></div>
                <div className="container max-w-5xl py-20 min-h-[800px] relative px-5">
                    <div className="bg-gradient-to-r from-[#4E018B] to-[#1685EC] min-h-[485px] flex flex-col mt-72 lg:flex-row rounded-[40px] relative z-20">
                        <div className="grow flex lg:block justify-center">
                            <img alt="" src="https://res.cloudinary.com/hammy06/image/upload/v1663711820/jukedome/jukewalk_ymvyl0.gif" className="absolute -top-80 md:-top-48 lg:top-auto h-[400px] lg:h-auto lg:-ml-32 lg:-mt-20 lg:h-[700px]" />
                        </div>
                        <div className="w-full lg:max-w-[685px] px-5 text-white flex items-center py-10 lg:py-0">
                            <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp">
                                <div className="flex flex-col space-y-5 md:space-y-10">
                                    <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold">Jukeboys</h2>
                                    <p className="text-lg md:text-xl font-medium">
                                        5,000 super wavy lads, embracing pop culture in order to
                                        devise an original strategy, by bringing pop culture and Music
                                        to CNFT. This tactic proposes a novel idea with the intention
                                        of bringing about a sea change to fuse an experience for
                                        social interactions and entertainment.
                                    </p>

                                    <div className="flex items-center space-x-5">
                                        <h3 className="text text-2xl lg:text-[40px] font-bold">Soldout</h3>

                                        <Button variant="secondary" link="https://www.jpg.store/collection/jukeboys-generation1" blank={true}>buy on jpeg store</Button>
                                    </div>
                                </div>
                            </AnimationOnScroll>
                        </div>
                    </div>
                </div>
                <div className="sphere3"></div>
            </div>


            <div className="min-h-[1500px] border border-blue-900 rounded-[40px] max-w-[1210px] mx-5 xl:mx-auto pt-10 pb-20 mb-48 bg-gradient-to-l from-[#2A30C1] to-[#0B237800]">
                <h3 className="text-4xl lg:text-[50px] font-bold text-center mb-24">Utility</h3>
                <div className="flex flex-col gap-y-14 px-[25px] lg:px-[150px]">
                    {utilityData.map((item, index) => (
                        <AnimationOnScroll animateOnce={true} animateIn={`${(index + 1) % 2 === 0 ? "animate__fadeInRight" : "animate__fadeInLeft"}`}>
                            <UtilityCard
                                icon={item.icon}
                                title={item.title}
                                description={item.description}
                                type={(index + 1) % 2 === 0 ? "2" : "1"}
                            />
                        </AnimationOnScroll>
                    ))}
                </div>
            </div>
            <div className="sphere4"></div>
            <div className="sphere5"></div>
            <div className="w-full bg-black relative">

                <div className="min-h-[861px] flex flex-col lg:flex-row items-center max-w-6xl justify-between mx-auto px-5 xl:px-0">
                    <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp">

                        <div className="lg:flex lg:flex-col lg:space-y-10 pt-20 pb-5 lg:py-0">
                            <h3 className="text-4xl lg:text-3xl font-bold">UTILITY TOKEN</h3>
                            <p className="font-medium capitalize hidden lg:block">
                                Our Party-to-Earn Ecosystem will use the $Juke token as its
                                currency. Holding Jukeboys NFT entitles you to earning $Jukes
                                passively and also via staking.
                            </p>
                            <div className="hidden lg:block">
                                <Button link="https://jukepaper.jukeboys.io/" blank={true}>View white paper</Button>
                            </div>
                        </div>
                    </AnimationOnScroll>
                    <img alt="" src="https://res.cloudinary.com/hammy06/image/upload/f_auto/v1663575100/jukedome/token_ppbsq9.svg" className="animate-pulse max-w-sm lg:max-w-full" />
                    <div className="pb-20 max-w-4xl lg:max-w-auto mx-auto px-5 md:px-0">
                        <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp">
                            <div className="lg:hidden flex flex-col space-y-10">
                                <p className="font-medium capitalize">
                                    Our Party-to-Earn Ecosystem will use the $Juke token as its
                                    currency. Holding Jukeboys NFT entitles you to earning $Jukes
                                    passively and also via staking.
                                </p>
                                <Button link="https://jukepaper.jukeboys.io/" blank={true}>View white paper</Button>
                            </div>
                        </AnimationOnScroll>
                    </div>
                </div>
            </div>
            <div className="text-center py-[150px]" id="roadmap">
                <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp">
                    <h3 className="text-center text-4xl font-bold lg:text-[50px] mb-[100px] lg:mb-[150px]">ROADMAP</h3>
                </AnimationOnScroll>


                <div className="flex justify-center uppercase space-x-10 text-center font-bold text-xs">
                    <div>
                        <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp"
                        >
                            <p className="mb-3">done</p>
                            <img alt="" src={greenLine} className="mx-auto" />
                        </AnimationOnScroll>
                    </div>
                    <div>
                        <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp" delay={100}>
                            <p className="mb-3">in progress</p>
                            <img alt="" src={blueLine} className="mx-auto" />
                        </AnimationOnScroll>
                    </div>
                    <div>
                        <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp" delay={200}>
                            <p className="mb-3">pending</p>
                            <img alt="" src={whiteLine} className="mx-auto" />
                        </AnimationOnScroll>
                    </div>
                </div>

                <div className="mt-[100px]">
                    <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInRight" delay={400}>
                        <img alt="" src={roadmap} className="mx-auto" />
                    </AnimationOnScroll>
                </div>
            </div>
            <div className="py-[100px] lg:py-[150px] px-5" id="faqs">
                <div className="sphere6"></div>
                <div className="max-w-6xl mx-auto bg-[#D9D9D90D]/5 flex flex-col items-center py-[50px] relative z-10 rounded-[20px] lg:rounded-[40px]">
                    <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp">
                        <h2 className="text-4xl lg:text-[50px] font-bold mb-14">FAQs</h2>
                    </AnimationOnScroll>
                    <div className="flex flex-col space-y-8 px-5 pb-5 lg:px-0 w-full">
                        {faqs.map((item, index) => (
                            <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp" delay={150} key={index}>
                                <div className="border-white/20 border-[3px] bg-white/10 rounded-[30px] cursor-pointer w-full max-w-[842px] mx-auto">
                                    <div className="flex justify-between items-center px-5 lg:px-[65px] py-5 lg:min-h-[73px] w-full space-x-5 lg:space-x-0" onClick={() => {
                                        if (activeFAQ === index) {
                                            setActiveFAQ(null)
                                        } else {
                                            setActiveFAQ(index)
                                        }
                                    }}>
                                        <p className="text-base lg:text-xl">{item.title}</p>
                                        <img alt="" src={chevronRight} className={`h-6 md:h-auto ${activeFAQ === index && "rotate-90"}`} />
                                    </div>
                                    {activeFAQ === index &&
                                        <p className="px-5 lg:px-[65px] pb-6 leading-8">
                                            {item.content}
                                        </p>
                                    }
                                </div>
                            </AnimationOnScroll>
                        ))}
                    </div>
                </div>
            </div>

            <div className="w-full">
                <div className="max-w-[1210px] py-32 mx-auto">
                    <h3 className="text-white text-4xl font-bold lg:text-[50px] text-center mb-0 lg:mb-[150px]">Assets</h3>
                    <div className="flex items-center h-[500px] px-5 space-x-5">
                        <div className="w-auto">
                            <img alt="" src={chevronLeft} onClick={prevSlide} />
                        </div>
                        <div className="grow">
                            {
                                slides.map((item, index) => {
                                    return (
                                        currentSlide === index && <img alt="" src={item} className="mx-auto" />
                                    )
                                })
                            }
                        </div>
                        <div className="w-auto">
                            <img alt="" src={chevronRight2} onClick={nextSlide} />
                        </div>
                    </div>
                </div>
            </div>

            <div className="bg-black" id="team">
                <div className="sphere7"></div>
                <div className="sphere8"></div>
                <div className="max-w-4xl mx-auto min-h-[1058px] py-14 lg:py-[150px] pb-[100px] lg:pb-[250px]">
                    <AnimationOnScroll animateOnce={true} animateIn="animate__fadeInUp">
                        <h3 className="font-bold text-4xl lg:text-[50px] text-center grid mb-14 lg:mb-32">
                            THE TEAM
                        </h3>
                    </AnimationOnScroll>
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 place-items-center gap-6 lg:gap-16">
                        {teams.map((item, index) => (
                            <AnimationOnScroll animateOnce={true} animateIn="animate__fadeIn" delay={(index * 100)}>
                                <TeamCard
                                    thumb={item.thumb}
                                    name={item.name}
                                    role={item.role}
                                    index={index}
                                    key={index}
                                />
                            </AnimationOnScroll>
                        ))}
                    </div>
                </div>
            </div>

            <div className="bg-[#190730]">
                <div className="max-w-6xl mx-auto text-center py-32 lg:py-64 px-5">
                    <iframe className="rounded-xl lg:rounded-3xl w-full max-w-[1038] h-[300px] lg:h-[585px]" src="https://www.youtube.com/embed/2LMR9lGGRNo" title="Unlock the Metaverse with Jukeboys" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
            </div>
        </div>
    );
};

export default LandingScreen;
